#container{
    width: 400px;
    background-color: aliceblue ;
    margin: 0 auto;
    padding: 20px;
}
input{
    width: 100%;
    padding: 5px;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
}
ul,li{
    margin: 0;
    padding: 0;
    list-style: none;
}
.list{
    margin: 15px 0;
}

.list>li{
    background-color: antiquewhite;
    padding: 5px;
    margin-bottom: 2px;
    display: flex;
    justify-content: space-between;
}
.btn{
    display: flex;
    justify-content: center;
    margin-top: 10px;

}
.btn>button{
    padding: 5px;
    background-color: darkseagreen;
    width: 100px;
    height: 40px;
    border-radius: 10px;
    border-color: darkgreen;
    font-weight: bold;
    font-size: x-large;
}
